import { isDevelopment } from '../prepare.build-tools.config'

export default (rules) => ([
	...rules,
	{
		test: /\.(ico|jpg|jpeg|png|gif|eot|otf|webp|svg|ttf|woff|woff2)(\?.*)?$/,
		loader: 'file-loader',
		query: {
			name: isDevelopment ? '[path][name].[ext]?[hash:8]' : '[hash:32].[ext]',
		},
	}
])
